@import 'reset';
@import 'mixin';
@import 'grid';
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);

$mainColor: #1e2642;
$textColor: #ffffff;
$hoverColor: #e0104d;
.modal-open {
    .modalSetting {
        filter: blur(0.5em);
        opacity: 0.8; } }
body {
    background-color: $mainColor;
    font-family: "PingFangTC","Noto Sans TC";
    overflow-x: hidden;
    .menu-on {
        filter: blur(0.5em);
        opacity: 0.8; }
    .wrap {
        margin: 0 auto;
        transition: filter 0.35s ease;
        .top {
            height: 1px; }
        .header {
            height: 85px;
            position: fixed;
            top: 0;
            width: 100%;
            background-color: #1e2642;
            z-index: 1000;
            .logo {
                @include hide-text;
                background-image: url(../Images/logo.png);
                background-size: contain;
                background-repeat: no-repeat;
                width: 134px;
                height: 63px;
                margin: 22px 0 0 40px;
                display: inline-block;
                @include iphone {
                    margin: 22px 0 0 16px; } }
            .menu {
                color: $textColor;
                font-family: "Helvetica","";
                font-size: 15px;
                text-decoration: none;
                position: absolute;
                top: 22px;
                right: 20px;
                z-index: 50;
                &:hover {
                    color: $hoverColor;
                    transition: all .3s; }
                .menuText {
                    padding-right: 13px;
                    @include iphone {
                        display: none; } } } }
        .main {
            color: $textColor;
            width: 80%;
            margin: 0 auto;
            margin-top: 255px;
            @include iphone {
                margin-top: 160px; }
            h2 {
                font-size: 52px;
                font-weight: 600;
                padding-bottom: 30px;
                @include iphone {
                    font-size: 46px;
                    line-height: 1.3; }
                br {
                    display: none;
                    @include iphone {
                        display: block; } } }
            hr {
                background-color: $textColor;
                height: 1px; }
            p {
                font-size: 18px;
                padding-top: 45px;
                letter-spacing: 2.6px;
                font-weight: 600;
                display: inline-block;
                @include pad {
                    display: block;
                    font-weight: normal; } } }
        .linkButton {
                display: inline-block;
                width: 212px;
                color: $textColor;
                border: solid 2px #ffffff;
                padding: 10px 20px;
                letter-spacing: 2.6px;
                text-decoration: none;
                margin-left: 60px;
                cursor: pointer;
                @include pad {
                    margin-left: 0;
                    margin-top: 30px; }
                &:hover {
                    color: $hoverColor;
                    border: $hoverColor solid 2px;
                    transition: all .3s; }
                i {
                    padding-left: 70px; } }
        .collection {
            margin-top: 215px;
            padding-top: 50px;
            @include iphone {
                margin-top: 100px; }
            .product {
                margin-top: 25px;
                position: relative;
                @include pad {
                    margin-top: 16px; }
                &:nth-child(odd) {
                    padding-right: 16px !important;
                    padding-left: 0px !important;
                    @include largeDesktop {
                        padding-right: 12px !important; }
                    @include desktop {
                        padding-right: 9px !important; }
                    @include iphone {
                        padding-right: 0px !important; } }
                &:nth-child(even) {
                    padding-left: 16px !important;
                    padding-right: 0px !important;
                    @include largeDesktop {
                        padding-left: 12px !important; }
                    @include desktop {
                        padding-left: 9px !important; }
                    @include iphone {
                        padding-left: 0px !important; } }
                .productButton {
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: cover; }
                .infoOn {
                    opacity: 0.5;
                    background-color: transparent;
                    transition: opacity 0.35s ease; }
                .productInfo {
                    display: none;
                    height: 100%;
                    width: calc(100% - 15px);
                    position: absolute;
                    top: 0;
                    transition: all 0.5s ease;
                    text-decoration: none;
                    .detail {
                        width: 75%;
                        height: 100%;
                        margin: 0 auto;
                        text-align: left;
                        h2 {
                            color: $textColor;
                            font-size: 20px;
                            letter-spacing: normal; }
                        p {
                            color: $textColor;
                            font-size: 14px;
                            margin-top: 1rem; }
                        hr {
                            background-color: $textColor;
                            height: 1px;
                            width: 100%; } } }
                a {
                    display: block;
                    height: 600px;
                    @include largeDesktop {
                        height: 360px; }
                    @include pad {
                        height: 430px; }
                    @include iphone {
                        height: 330px; }
                    img {
                        width: 100%;
                        height: 100%; } } } }
        .aboutMe {
            color: $textColor;
            padding-top: 55px;
            h2 {
                width: 11%;
                min-width: 163px;
                margin-left: 15.2%;
                font-size: 52px;
                font-weight: 600;
                padding-top: 40px;
                @include iphone {
                    margin-left: 11.2%; } }
            hr {
                background-color: $textColor;
                height: 2px;
                width: 11%;
                min-width: 155px;
                margin-left: 15.2%;
                @include iphone {
                    margin-left: 11.2%; } }
            p {
                font-size: 18px;
                line-height: normal;
                margin: 30px 15.2%;
                font-weight: lighter;
                @include iphone {
                    margin: 30px 11.2%;
                    font-weight: lighter;
                    font-size: 14px; } }
            .resumeButton {
                margin-left: 15.2%;
                margin-bottom: 65px;
                @include iphone {
                    margin-left: 11.2%; } } }
        .collectModal {
            .modal {
                padding-right: 0px !important; }
            .modal-dialog {
                max-width: 70% !important;
                @include pad {
                    max-width: 90% !important; } }
            .modalWrap {
                margin: 0 auto;
                position: relative;
                top: 10%;
                padding: 75px;
                border-radius: 30px;
                background-color: rgba(255, 255, 255, 0.9);
                @include pad {
                    padding: 40px; } }
            .close {
                position: absolute;
                top: -55px;
                right: -55px;
                cursor: pointer;
                @include pad {
                    top: -20px;
                    right: -20px; } }
            .collectImg {
                .box {
                    width: 47.5%;
                    display: inline-block;
                    @include iphone {
                        width: 100%;
                        display: block;
                        margin-bottom: 10px; }
                    img {
                        max-width: 100%;
                        height: auto;
                        cursor: zoom-in; } }
                .firstImg {
                    padding-bottom: 20px;
                    @include iphone {
                        display: block !important;
                        padding-bottom: 0px; } }
                .secondImg {
                    @include iphone {
                        display: block !important; } } }
            .modal-content {
                background-color: transparent;
                border: none;
                h2 {
                    letter-spacing: 0.5px;
                    color: #1e2642;
                    font-size: 42px;
                    font-weight: 600;
                    width: 265px;
                    border-bottom: 2px solid #1e2642;
                    padding-bottom: 25px;
                    @include iphone {
                        width: 80%; } }
                h3 {
                    font-size: 24px;
                    color: #1e2642;
                    padding: 20px 0 15px 0; }
                p {
                    padding-bottom: 75px;
                    line-height: normal;
                    font-size: 14px; } } } }
    .active {
        display: block !important; }
    .lightcase-icon-close {
        i {
            color: white; } }
    .menuOpen {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        display: none;
        .closeButton {
            position: absolute;
            top: 20px;
            right: 20px;
            color: $textColor;
            background-color: transparent;
            border: none;
            font-size: 20px;
            &:hover {
                transition: 0.3s color;
                color: $hoverColor; } }
        ul {
            width: 240px;
            margin: 0 auto;
            text-align: center;
            margin-top: 10%;
            .borderSetting {
                border-bottom: solid 1px rgba(212, 212, 255, 0.5); }
            .buttonSetting {
                padding-top: 0; }
            li {
                font-size: 18px;
                font-weight: 600;
                letter-spacing: 2.6px;
                padding: 13px 0px;
                a {
                    color: $textColor;
                    text-decoration: none;
                    &:hover {
                        color: $hoverColor;
                        transition: all .3s; } }
                .buttonInMenu {
                    display: inline-block;
                    color: $textColor;
                    border: solid 2px #ffffff;
                    padding: 10px 20px;
                    letter-spacing: 2.6px;
                    text-decoration: none;
                    margin-left: 0;
                    text-align: left;
                    &:hover {
                        border: solid 2px $hoverColor; }
                    i {
                        padding-left: 65px; } } } } } }
