@mixin hide-text {
    text-indent: 110%;
    white-space: nowrap;
    overflow: hidden; }
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both; } }
@mixin circle($size,$bgcolor) {
    display: inline-block;
    width: $size;
    height: $size;
    border: 1px solid #fff;
    margin: 0 5px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    background: $bgcolor; }
